/* Prevent Scrolling */
html, body {
  overflow: hidden;
  height: 100vh;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

/* Container */
.sch_container {
  display: flex;
  width: 100vw;
  height: 100vh;
  background: #f8f8f8;
}

/* Sidebar - 25% */
.sch_sidebar {
  width: 25%;
  background: #fff;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-right: 2px solid #ddd;
  overflow-y: auto;
  height: 100vh;
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.1);
}

/* Main Content - 75% */
.sch_main {
  width: 75%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  overflow-y: auto;
  height: 100vh;
  background: #f8f8f8;
}

/* Title */
.sch_title {
  font-size: 24px;
  font-weight: bold;
  color: #333;
  margin-bottom: 20px;
  text-align: center;
}

/* Filter Section */
.sch_filter {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

/* Input Boxes */
.sch_input {
  width: 100%;
  padding: 10px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 8px;
  margin-bottom: 10px;
  outline: none;
  transition: border-color 0.3s ease;
}

.sch_input:focus {
  border-color: #ff7b00;
}

/* Suggestions */
.sch_suggestions {
  position: absolute;
  background: white;
  border: 1px solid #ccc;
  border-radius: 8px;
  width: calc(100% - 20px);
  max-height: 150px;
  overflow-y: auto;
  z-index: 10;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.sch_suggestion-item {
  padding: 10px;
  cursor: pointer;
  font-size: 14px;
  color: #333;
  transition: background 0.3s ease;
}

.sch_suggestion-item:hover {
  background: #f0f0f0;
}

/* Dropdown */
.sch_dropdown {
  width: 100%;
  padding: 10px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 8px;
  margin-bottom: 10px;
  outline: none;
  transition: border-color 0.3s ease;
}

.sch_dropdown:focus {
  border-color: #ff7b00;
}

/* Age Filter */
.sch_age-filter {
  display: flex;
  flex-direction: column;
  gap: 10px;
  width: 100%;
  margin-bottom: 20px;
}

.sch_age-filter label {
  font-size: 14px;
  color: #333;
}

.sch_range-slider {
  width: 100%;
  margin-top: 5px;
}

/* Buttons */
.sch_btn {
  background: #ff7b00;
  color: white;
  padding: 12px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 14px;
  transition: background 0.3s ease;
  width: 100%;
  margin-bottom: 10px;
}

.sch_btn:hover {
  background: #e56a00;
}

/* Dog Grid */
.sch_dog-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 15px;
  width: 100%;
  max-width: 1000px;
  margin: 0 auto;
  padding-bottom: 20px;
}

/* Pagination */
.sch_pagination {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-top: 20px;
}

.sch_page-btn {
  background: #ff7b00;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 14px;
  transition: background 0.3s ease;
}

.sch_page-btn:disabled {
  background: #ccc;
  cursor: not-allowed;
}

.sch_page-btn:hover:not(:disabled) {
  background: #e56a00;
}

.sch_page-info {
  font-size: 14px;
  color: #333;
  display: flex;
  align-items: center;
}

/* Match Result */
.sch_match-result {
  margin-top: 20px;
  text-align: center;
}

.sch_match-result h3 {
  font-size: 18px;
  font-weight: bold;
  color: #333;
  margin-bottom: 10px;
}

/* Responsive Design */
@media (max-width: 768px) {
  .sch_container {
    flex-direction: column;
  }

  .sch_sidebar {
    width: 100%;
    border-right: none;
    border-bottom: 2px solid #ddd;
    height: auto;
    overflow-y: visible;
  }

  .sch_main {
    width: 100%;
    height: auto;
    overflow-y: visible;
  }

  .sch_dog-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 480px) {
  .sch_dog-grid {
    grid-template-columns: repeat(1, 1fr);
  }
}